import React from "react"
import {NavLink} from "react-router-dom"

function Header(){
    return(
        <header className="header-nav">
            <div className="header">
                <nav className="navcenter">
                    <h1 className="header-logo">
                        <NavLink to="/admin" style={{display:"block",width:"100%",height:"100%",backgroundColor:"transparent"}}></NavLink>
                    </h1>
                    <div className="headerNav">
                        <NavLink to="/admin" key={""} ><em></em></NavLink>
                        <NavLink to="/my" key={""} ><em></em></NavLink>
                        <NavLink to="/friend" key={""} ><em></em></NavLink>
                        <NavLink to="/download" key={""}  className="header-downLoad"><span className="hot"></span><em></em></NavLink>
                    </div>
                    <div className="header-login"><a href="#"></a></div>
                    <a href="#" className="header-creator"></a>
                    <div className="header-search">
                        <input type="text" className="search" value="///" onChange={()=>{
                            return ""
                        }}/>
                    </div>
                </nav>
            </div>
        </header>
    )
}

export default Header;